.Documents {
  width: 65%;
  height: 700px;
  background-color: lightgray;
  margin: 1% auto;
  border-radius: 10px;
  border: 1px solid;
}
.pointsSheet {
  width: 100%;
  //   overflow: scroll;
  padding: 10px;
  margin-top: 1%;
  // margin: auto;
  height: 1850px;
  //   background-color: red;
  
}
.PointSheetTable {
  font-family: arial, sans-serif;
  border-collapse: collapse;
  width: 100%;
  margin-top: 1%;
  background-color: lightgray;
  // padding: 10px;
}
td,
th {
  border: 4px solid white;
  text-align: left;
  padding: 5px;
  font-size: medium;
}
.PayOffTable {
  height: auto;
  margin-bottom: 100px;
  // background-color: blue;
}

@media screen and (min-width: 1680px) {
  .pointsSheet {
    width: 80%;
  }
}

@media screen and (max-height: 480px) {
  .pointsSheet {
    width: 70%;
  }
}
@media screen and (max-width: 480px) {
  .pointsSheet {
    width: 80%;
    height: 1700px;
    padding: 0px;
    overflow: none;
    .PointSheetTable{
      width: 200px;
    }
  }
}
