.topNav {
  width: 100%;
  height: 7vh;
  display: grid;
  grid-template-columns: 100px 300px auto;
  background-color: #043461;
  // position: absolute;
  padding-top: 1%;
  // z-index: 99;
  // top: 0%;
  .closeIcon {
    font-size: 40px;
    color: gray;
    margin: auto;
    // display: none;
  }
  .logo {
    width: 40%;
    // position: absolute;
    // left: 1%;
    margin-left: 10%;
  }
  p {
    color: white;
    // margin-left: -180%;
    // position: absolute;
    // left: 8%;
    // top: 12%;
  }
}
.start-container {
  display: grid;
  width: 80%;
  // column-gap: 10px;
  grid-template-columns: auto auto;
  // background-color: #2196F3;
  padding: 10px;
  margin: 10% auto;
}

.start-item {
  // border: 1px solid rgba(0, 0, 0, 0.8);
  padding: 10px;
  font-size: 30px;
  text-align: left;
  button {
    width: 180px;
    height: 45px;
    background-color: #3d75d4;
    color: white;
    border-radius: 50px;
    font-size: 20px;
    border: none;
  }
  h2 {
    font-weight: normal;
  }
  img {
    width: 100%;
  }
}

@media screen and (max-width: 480px) {
  .topNav {
    width: 100%;
    height: 80px;
    background-color: #043461;
    position: absolute;
    display: grid;
    grid-template-columns: auto auto auto;
    top: 0%;

    .logo {
      width: 40px;
      margin: auto;
      // position: absolute;
      // left: -5%;
      // top: 10px;
    }
    p {
      color: white;
      // position: absolute;
      // left: 20%;
      margin: auto;
      // top: 20%;
      font-size: 12px;
    }
    .closeIcon {
      font-size: 40px;
      color: gray;
      margin: auto;
      // display: none;
    }
  }
  h1 {
    margin-top: 5%;
    font-size: 20px;
  }
  h2 {
    font-size: 15px;
  }

  .start-container {
    display: grid;
    width: 80%;
    // background-color: #043461;
    // column-gap: 10px;
    grid-template-columns: auto;
    // background-color: #2196F3;
    padding: 10px;
    margin: 20% auto;
    .start-item {
      // background-color: #3d75d4;
      width: 100%;
      height: auto;
      // margin-top: 20%;
      img {
        width: 80%;
      }
      button {
        position: absolute;
        top: 55%;
      }
    }
  }
}

