.result {
  width: 120%;
  height: 50vh;
  margin: auto;
  background-color: lightgray;
  text-align: center;
  // border-radius: 20px;
}
.graph {
  margin: auto;
  width: 90%;
  height: 40vh;
}
.DivP{
  p{
    color: white;
  }
}
@media screen and (max-height: 480px) {
  .testing{
    display: grid;
    grid-template-columns: auto;
  }
  .result {
    width: 100%;
    height: 350px;
    margin-left: -1%;
    text-align: center;
    border-radius: 20px;
  }
  .graph {
    margin: auto;
    width: 98%;
    height: 300px;
  }
  .DivP{
    p{
      color: black;
    }
  }
}


